import axios from "axios";
import { createStore } from "vuex";

const store = createStore({
    state() {
        return {
            name: 'kim',
            age: 20,
            likes: 30,
            checkLikes: false,
            more: {},
            chats: [{
                    image: "cat1.jpg",
                    title: "중계팀 소개",
                    subtitle: "중계팀이 어떤 사역을 하는지 알려줄게!",
                    time: "17:00",
                },
                {
                    image: "cat2.png",
                    title: "카메라 사역 소개",
                    subtitle: "카메라 사역에 대해 알아볼까? 😊",
                    time: "15:30",
                },
                {
                    image: "cat3.jpg",
                    title: "중계 사역 소개",
                    subtitle: "중계 사역에 대해 소개해보려고 해 😀",
                    time: "11:00",
                },
                {
                    image: "cat4.jpg",
                    title: "중계 사역 타임테이블",
                    subtitle: "중계 사역에 대해 소개해보려고 해 😀",
                    time: "10:30",
                },
                {
                    image: "cat5.jpg",
                    title: "예배 프로세스",
                    subtitle: "중계 사역에 대해 소개해보려고 해 😀",
                    time: "09:55",
                },
            ],
        }
    },
    mutations: {
        /* 
            변경 함수 정의 
            axios.get().then() 하면 되지 않을까요
            1.name을 바꾸고  mutation 함수 3초
            2.age를 바꾸려면 mutation 함수 4초 
        
        */

        setMore(s, data) {
            s.more = data
        },
        changeName(state) {
            state.name = 'park'
        },
        addAge(state, payload) {
            state.age += payload;
        },
        increaseLike(state) {
            if (state.checkLikes == false) {
                state.likes++;
                state.checkLikes = true;

            } else {
                state.likes--;
                state.checkLikes = false;
            }
        },
    },
    actions: {
        /* 
            ajax를 요청하는 곳  

            dispatch('getData')을 가져온다
            다른 컴포넌트와 공유하고 싶으면 
            ajax는 action : dispatch 
            state변경 mutations : commit 
        */
        getData(context) {
            axios.get('https://codingapple1.github.io/vue/more0.json')
                .then((a) => {
                    console.log(a.data);
                    context.commit('setMore', a.data);
                })
        }
    },

})

export default store;